.tasks_text {
  color: var(--primary-text-color);
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  margin-bottom: 24px;
}
.chart_done {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin-left: 170px; */
}
.bar_day {
  width: 14px;
  background: linear-gradient(
    360deg,
    #ffd2dd 0%,
    rgba(255, 210, 221, 0) 96.88%
  );
  margin-right: 18px;
  margin-bottom: 5px;
  position: relative;
}
.bar {
  width: 14px;
  background: linear-gradient(360deg, #3e85f3 0%, rgba(62, 133, 243, 0) 100%);
  margin-bottom: 5px;
  position: relative;
}

.bar-label {
  color: var(--primary-text-color);
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
}

.nameBar {
  color: var(--primary-text-color);
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  margin-top: 5px;
  position: absolute;
  text-align: center;
  /* margin-left: 176px; */
  /* margin-bottom: -20px; */
}
